@import url("https://fonts.googleapis.com/css2?family=Prompt&display=swap");
nav ul {
  padding: 0;
  list-style-type: none;
  width: 100%;
}
nav ul .liFrame {
  position: relative;
}
nav ul .liFrame li {
  width: inherit;
  height: 50px;
  background-color: rgb(53, 62, 112);
  color: white;
  text-align: center;
  padding-top: 12px;
  border-bottom: 1px dashed white;
  margin: auto;
  cursor: pointer;
}
nav ul .liFrame .del-navItem {
  position: absolute;
  right: 0;
  top: 0;
  height: calc(100% - 2px);
  width: 40px;
  background: red url(../public/delete.png) no-repeat center;
  background-size: 30px 30px;
}

.active {
  background-color: rgb(72, 84, 151);
}

.strike {
  text-decoration: line-through;
}

section .todoFrame {
  background: rgb(253, 253, 123) url(../public/pin.png) no-repeat 50% 0px;
  background-size: 60px;
  max-width: 500px;
  width: 100%;
  max-height: 500px;
  height: 100%;
}
section .todoFrame h2 {
  padding: 50px;
  text-align: center;
}
section .todoFrame ul {
  padding: 50px;
  font-family: "Prompt", sans-serif;
  border-radius: 10px;
  height: 100%;
  margin: auto;
  list-style-type: none;
}
section .todoFrame ul div {
  margin-top: 10px;
  width: 100%;
  min-height: 50px;
  height: fit-content;
  display: inline-flex;
  align-items: center;
}
section .todoFrame ul div li {
  margin: auto;
  padding: 10px;
  width: 80%;
  float: left;
  height: 100%;
  border: 1px dashed black;
  border-right: none;
  border-radius: 10px 0px 0px 10px;
}
section .todoFrame ul div button {
  width: 20%;
  float: right;
  display: block;
  height: 100%;
  max-height: fit-content;
  min-height: 50px;
  font-size: inherit;
  background: none;
  border-radius: 0px 10px 10px 0px;
  border: 1px solid red;
  color: red;
  word-break: break-all;
}

form {
  max-width: 500px;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  nav {
    width: 100%;
    max-width: 100%;
    height: fit-content;
  }
  nav ul li {
    height: 50px;
  }
  section {
    width: 100%;
    height: fit-content;
  }
  section .todoFrame {
    margin-top: 50px;
  }
}

